/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  min-width: 30rem;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito Sans", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3, h4 {
  font-family: "Nunito Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  p {
    font-size: 1.6rem;
  }
}

@media (min-width: 768px) {
  .pantalla-login {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 92vh;
  }
}
.pantalla-login .imagen {
  height: 15rem;
  background-image: url("../img/login.jpg");
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .pantalla-login .imagen {
    height: auto;
    clip-path: polygon(20% 0, 100% 0, 100% 75%, 80% 100%, 0 100%, 0 25%);
  }
}
.pantalla-login .formulario-login {
  background-color: #0da6f3;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 70%;
  height: 90%;
  border-radius: 1rem;
  padding: 2rem 1rem;
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .pantalla-login .formulario-login {
    margin: auto;
  }
}
.pantalla-login .formulario-login p:not(.condiciones) {
  text-align: center;
  font-size: 1.8rem;
}
.pantalla-login .formulario-login form {
  width: 90%;
  margin: 0 auto;
}

.options {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.options a, .options input {
  background-color: #FFFFFF;
  text-align: center;
  color: #000000;
  font-weight: 700;
  padding: 1rem;
  margin: 1rem auto;
  border-radius: 1rem;
  width: 100%;
  transition: all 200ms ease;
}
.options a:hover, .options input:hover {
  background-color: #ecebeb;
}
.options input {
  border: none;
  width: 90%;
}
.options input[type=submit] {
  cursor: pointer;
}

.condiciones {
  font-size: 1.4rem;
  margin: 0.5rem 2rem;
  text-align: center;
  line-height: auto;
}
.condiciones a {
  color: #FFFFFF;
}

.sombra {
  -webkit-box-shadow: 0px 0px 17px 10px rgba(0, 0, 0, 0.16);
  -moz-box-shadow: 0px 0px 17px 10px rgba(0, 0, 0, 0.16);
  box-shadow: 0px 0px 17px 10px rgba(0, 0, 0, 0.16);
}

.header {
  background-color: #000000;
}
.header .contenedor .barra {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header .contenedor .barra .navegacion a {
  display: block;
  text-align: center;
  background-color: #FFFFFF;
  padding: 1rem;
  margin: 1rem auto;
  color: #0da6f3;
  border-radius: 1rem;
  font-weight: 700;
  transition: all 250ms ease;
}
.header .contenedor .barra .navegacion a:hover {
  background-color: #0da6f3;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header .contenedor {
    height: 6rem;
  }
  .header .contenedor .barra {
    height: 100%;
    flex-direction: row;
    justify-content: space-between;
  }
  .header .contenedor .barra a {
    height: 80%;
  }
  .header .contenedor .barra a img {
    height: 100%;
  }
  .header .contenedor .barra .navegacion {
    display: flex;
  }
  .header .contenedor .barra .navegacion a {
    margin-left: 2rem;
  }
  .header .contenedor .barra .navegacion a:first-of-type {
    margin-left: 0;
  }
}

/*menu de hamburguesa*/
.mobile-menu {
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  display: block;
}

.footer {
  background-color: #000000;
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer p {
  color: #FFFFFF;
}